import { PAGE_SIZE } from '@/assets/config'
import { CODE, RulesRegulationsKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'rules',
  data () {
    return {
      code: CODE.RulesRegulations,
      dataKeys: RulesRegulationsKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {
        page_num: PAGE_SIZE
      },
      searchOptions: [
        { label: '时间', key: 'time', type: 'date' },
        { label: '主题', key: 'theme' },
        { label: '类别', key: 'type' }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables()
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      this.pageSize = this.condition.page_num
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.RulesRegulations.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.RulesRegulations.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/rules/new', '规章制度-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/rules/' + item.id, '规章制度-详情-' + item.id)
    }
  },
  created () {
    this.operation.export = true
    if (Cache.hasPermission('management_work')) {
      this.operation.add = true
      this.operation.del = true
      this.operation.edit = true
    }
    if (Cache.hasPermission('content_management')) {
      this.operation.approval = true // 审批权限
    }
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
    })
    if (this.$route.query['key']) {
      this.condition['key'] = this.$route.query['key'];this.condition['status'] = 3
    }
    this.getDataTables()
  },
  mounted () {
  }
}



